.gallery {
    padding-top: 70px;
    margin-top: 15px;
    background: url(../img/advantages-bg.png) no-repeat top center / cover;
    @include r($mobile) {
        background-position: top left -192px;
        padding-top: 81px;
        margin-top: -33px; } }
.gallery__section-title {
    padding-bottom: 0;
    margin-bottom: 30px;
    @include r($mobile) {
        margin-bottom: 20px; }
    &::after {
        display: none; } }
.gallery__wrap {
    display: flex;
    justify-content: space-between;
    @include r($desktop) {
        flex-direction: column;
        align-items: center; } }
.gallery__wrap-slider {
    position: relative;
    width: calc( 50% - 10px );
    @include r($desktop) {
        max-width: 580px;
        width: 100%;
        padding-bottom: 70px;
        &:last-child {
            margin-top: 50px; } }
    @include r($mobile) {
        padding-bottom: 44px;
        &:last-child {
            margin-top: 30px; } } }
.gallery__slide-img {
    height: 328px;
    width: 100%;
    object-fit: cover;
    display: block;
    border-radius: 20px;
    @include r($mobile) {
        height: 225px; } }
.gallery__slide-desc {
    position: absolute;
    z-index: 3;
    box-shadow: 0 6px 15px 1px rgba(133, 133, 133, 0.35);
    padding: 8px;
    border-radius: 36px;
    background-color: #ffffff;
    bottom: 0;
    transform: translateY(50%) translateX(-50%);
    max-width: 420px;
    width: 100%;
    left: 50%;

    color: #464646;
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.18px;
    text-align: center;
    @include r($mobile) {
        font-size: 16px;
        line-height: 26px;
        letter-spacing: -0.08px;
        padding: 10px 8px; } }
.gallery__slide-fullscreen {
    position: absolute;
    top: -1px;
    right: -1px;
    width: 45px;
    height: 45px;
    background-color: #fff;
    border-radius: 0 20px 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
    transition: 0.3s;
    &:hover {
        opacity: 1; }
    &::before {
        content: url(../img/fullscreen.svg);
        width: 20px;
        display: block; } }
.gallery__slider {
    padding-bottom: 56px;
    @include r($mobile) {
        width: calc( 100% + 20px );
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        padding-left: 10px;
        padding-right: 10px; } }
.gallery__slider-btn-prev, .gallery__slider-btn-next {
    width: 70px;
    height: 70px;
    @include r($mobile) {
        width: 52px;
        height: 52px;
        bottom: 0; }
    svg {
        width: 27px;
        height: 27px;
        @include r($mobile) {
            width: 23px;
            height: 23px; } } }
.gallery__slider-btn-prev {
    left: calc( 50% - 110px );
    @include r($mobile) {
        left: calc( 50% - 80px ); } }
.gallery__slider-btn-next {
    right: calc( 50% - 110px );
    @include r($mobile) {
        right: calc( 50% - 80px ); } }

.gallery__slider_video {
    .swiper-slide {
        position: relative;
        &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(#030303,0.21);
            border-radius: 20px; } }
    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        border-radius: 20px; } }
.play-btn {
    background: url(../img/btn-play.png) no-repeat center / 100% 100%;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    height: 120px;
    z-index: 2;
    opacity: 0.8;
    transition: 0.3s;
    @include r($mobile) {
        width: 100px;
        height: 100px; }
    &:hover {
        opacity: 1; } }
