.offers {
    margin-top: 91px;
    @include r($tablet) {
        margin-top: 45px; } }
.offers__section-title {
    margin-bottom: 20px;
    @include r($mobile) {
        margin-bottom: 15px; } }
.offers__subtitle {
    color: #464646;
    font-family: "RotondaC";
    font-size: 24px;
    font-weight: 400;
    letter-spacing: 1.2px;
    text-align: center;
    @include r($mobile) {
        font-size: 18px;
        letter-spacing: 0.9px;
        line-height: 28px; } }
.offers__wrap {
    margin-top: 14px;
    display: flex;
    flex-wrap: wrap;
    @include r($mobile) {
        justify-content: space-between;
        margin-top: 0;
        max-width: 300px;
        width: 100%;
        margin-left: auto;
        margin-right: auto; } }
.offers__item {
    width: calc( 100% / 6 );
    margin-top: 27px;
    padding: 0 5px;
    @include r($wide) {
        width: 20%; }
    @include r($desktop) {
        width: 25%; }
    @include r($tablet) {
        width: calc( 100% / 3 ); }
    @include r($mobile) {
        width: 143px;
        padding: 0;
        margin-top: 20px; } }
.offers__item-wrap-icon {
    margin: 0 auto 15px auto;

    width: 160px;
    height: 160px;
    padding: 20px;
    position: relative;
    border-radius: 50%;
    box-shadow: 0 4px 13px rgba(123, 123, 123, 0.12);
    background-color: rgba(#ffffff,0.37);
    @include r($mobile) {
        padding: 17px;
        width: 142px;
        height: 142px; }
    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        box-shadow: 0 4px 13px rgba(123, 123, 123, 0.3);
        background-color: #ffffff; } }
.offers__item-icon {
    max-width: 76px;
    max-height: 58px;
    display: block;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%); }
.offers__item-txt {
    text-align: center; }
