input[type="text"],
input[type="tel"],
input[type="email"],
textarea {
  -webkit-appearance: none;
  color: #ffffff;
  font-family: "OpenSans";
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.18px;
  &::-webkit-input-placeholder {
    color: #ffffff;
    font-family: "OpenSans";
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.18px; }
  &::-moz-placeholder {
    color: #ffffff;
    font-family: "OpenSans";
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.18px; }
  &:-ms-input-placeholder {
    color: #ffffff;
    font-family: "OpenSans";
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.18px; } }
input[type="text"],
input[type="tel"],
input[type="email"], {
  padding: 0 20px;
  width: 100%;
  height: 54px;
  border: none;
  background-color: transparent;
  border-radius: 27px; }
input[type=number] {
  -moz-appearance: textfield;
  -moz-appearance: none;
  appearance: none;
  margin: 0; }
