* {
  box-sizing: border-box;
  outline: none; }

body, html {
  background: white;
  color: $fontColor;
  font-family: $font-family;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.page-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  max-width: 1920px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden; }

.container {
  max-width: 1180px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include r($wide) {
    max-width: 960px;
    padding-left: 15px;
    padding-right: 15px; }
  @include r($desktop) {
    max-width: 720px; }
  @include r($tablet) {
    max-width: 540px; }
  @include r($mobile) {
    max-width: 100%;
    padding-left: 10px;
    padding-right: 10px; } }
h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
  padding: 0;
  margin: 0; }
a {
  color: $fontColor;
  text-decoration: none;
  font-family: $font-family;
  font-size: 18px;
  font-weight: 400;
  &:hover, &:focus {
    text-decoration: none; } }
ul {
  list-style: none; }
ol {
  list-style: decimal; }
img.svg {
	display: none; }
.swiper-button-next, .swiper-button-prev {
  transform: none;
  margin: 0;
  padding: 0;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
  background: none;
  color: transparent;
  &::after {
    display: none; } }
.section-title {
  color: #464646;
  font-family: "RotondaC";
  font-size: 40px;
  font-weight: 700;
  line-height: 61px;
  letter-spacing: 0.4px;
  text-align: center;
  padding-bottom: 15px;
  position: relative;
  @include r($mobile) {
    font-size: 25px;
    letter-spacing: 0.25px;
    line-height: 35px; }
  &::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 2px;
    background-color: #fc0b1f; } }
.section-title_color_red {
  color: #fc0b1f; }
.section-title_color_white {
  color: #fff; }
p {
  color: #333333;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.18px; }

.slider-btn-next, .slider-btn-prev {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 2px solid #fc0b1f;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.3s;
  svg {
    fill: #fc0b1f;
    transition: 0.3s; }
  &:hover {
    background-color: #fc0b1f;
    svg {
      fill: #fff; } } }
.slider-btn-prev {
  transform: rotate(180deg); }
.slider-btn_disabled {
  opacity: 0.5; }
