.burger {
  display: none;
  width: 30px;
  height: 22px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  z-index: 2;
  .icon-bar {
    transition: 0.1s;
    width: 100%;
    height: 2px;
    background-color: #fb0a1f;
    @include r($mobile) {
      background-color: #fff; } }
  @include r($desktop) {
    display: flex; }
  @include r($mobile) {
    position: relative;
    right: 0;
    transform: none; } }
.burger.active {
  .icon-bar:nth-child(1), .icon-bar:nth-child(3) {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 90%;
    background-color: #000; }
  .icon-bar:nth-child(2) {
    opacity: 0; }
  .icon-bar:nth-child(1) {
    transform: translate(-50%, -50%) rotate(45deg); }
  .icon-bar:nth-child(3) {
    transform: translate(-50%, -50%) rotate(-45deg); } }
