.faq {
    margin-top: 137px;
    @include r($desktop) {
        margin-top: 40px; } }
.faq__section-title {
    padding-bottom: 0;
    &::after {
        display: none; } }
.faq__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    @include r($tablet) {
        flex-direction: column; }
    @include r($mobile) {
        margin-top: 18px; } }
.faq__l, .faq__r {
    width: calc( 50% - 10px );
    @include r($tablet) {
        width: 100%; } }
.faq__l {
    @include r($tablet) {
        margin-bottom: 20px; } }
.faq__accordion {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0; } }
