.section-form {
    min-height: 263px;
    background: url(../img/section-form-bg.jpg) no-repeat top center / cover;
    padding: 37px 0 49px 0;
    @include r($mobile) {
        background-image: url(../img/section-form-bg-mob.jpg); } }
.section-form__title {
    padding-bottom: 0;
    @include r($mobile) {
        font-size: 30px;
        margin-bottom: 10px; }
    &::after {
        display: none; } }
.section-form__subtitle {
    color: #ffffff;
    font-family: "RotondaC";
    font-size: 30px;
    font-weight: 400;
    line-height: 50px;
    text-align: center;
    margin-bottom: 15px;
    @include r($mobile) {
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 25px; } }
.section-form__wrap-form {
    @include r($desktop) {
        max-width: 450px;
        width: 100%;
        margin-left: auto;
        margin-right: auto; } }
.section-form_margin-top {
    margin-top: 97px;
    @include r($tablet) {
        margin-top: 60px; } }
.section-form__section-title-uppercase {
    color: #ffffff;
    font-family: "RotondaC";
    font-size: 30px;
    font-weight: 700;
    line-height: 50px;
    letter-spacing: 0.3px;
    text-align: center;
    margin-bottom: 23px;
    @include r($mobile) {
        font-size: 22px;
        line-height: 34px;
        letter-spacing: 0.22px; }
    span {
        color: #a8000e;
        font-size: 40px;
        letter-spacing: 0.4px;
        @include r($mobile) {
            font-size: 30px;
            letter-spacing: 0.3px; } } }
