.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0); }
html.mfp-helper {
  height: 100%;
  body {
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0); } }
.mfp-close-btn-in .mfp-close {
  // style
  top: -10px;
  right: -40px;
  background: url(../img/close.svg) no-repeat center;
  width: 25px;
  height: 25px;
  color: transparent;
  filter: invert(1);
  @include r($tablet) {
    top: 20px;
    right: 20px;
    filter: none;
    width: 18px;
    height: 18px;
    background-size: 100% 100%; } }
.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  width: inherit;
  min-width: 33px; }
.white-popup-block {
  max-width: 580px;
  width: 100%;
  padding: 45px 80px 35px 80px;
  text-align: left;
  margin: auto;
  position: relative;
  background-color: #ffffff;

  border-radius: 30px;
  @include r($mobile) {
    padding: 45px 28px 45px 28px; }
  @include rh(580) {
    padding-bottom: 20px; }
  form {
    max-width: 380px;
    width: 100%;
    margin-top: 16px;
    margin-left: auto;
    margin-right: auto;
    @include r($mobile) {
      margin-top: 22px; } } }
.popup-buble {
  .popup-dialog {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8); }
  &.mfp-ready .popup-dialog {
    opacity: 1;
    transform: scale(1); }
  &.mfp-removing .popup-dialog {
    transform: scale(0.8);
    opacity: 0; }
  &.mfp-bg {
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: 0.3s; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }
.modal-content__section-title {
  padding-bottom: 0;
  @include r($mobile) {
    font-size: 30px; }
  &::after {
    display: none; } }
.modal-content__ui-group {
  margin-bottom: 20px;
  @include rh(580) {
    margin-bottom: 10px; }
  input[type="text"],input[type="email"],input[type="tel"],textarea {
    border: 1px solid #404040;
    color: #404040;
    &::-webkit-input-placeholder {
      color: #9d9d9d; }
    &::-moz-placeholder {
      color: #9d9d9d; }
    &:-ms-input-placeholder {
      color: #9d9d9d; } }
  input[type="text"],input[type="email"],input[type="tel"] {
    height: 54px;
    @include rh(580) {
      height: 44px; } }
  textarea {
    padding: 20px;
    border-radius: 27px;
    resize: none;
    width: 100%;
    height: 181px;
    @include rh(580) {
      height: 80px; } } }
.modal-content__ui-group-textarea, .modal-content__ui-group-politic, .modal-content__ui-group-submit {
  margin-bottom: 0; }
.modal-content__ui-group-politic {
  margin-top: 12px;
  @include rh(580) {
    margin-top: 8px; }
  input[type="checkbox"] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    &:checked + .wpcf7-list-item-label {
      &::after {
        opacity: 1; } } } }
.wpcf7-list-item-label {
  cursor: pointer;
  color: #000000;
  font-family: "OpenSans";
  font-size: 15px;
  font-weight: 400;
  line-height: 22px;
  padding-left: 32px;
  position: relative;
  display: block;
  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 0;
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 2px;
    border: 1px solid #3e3e3e; }
  &::after {
    content: '';
    display: block;
    position: absolute;
    background-color: #3e3e3e;
    width: 12px;
    height: 12px;
    border-radius: 2px;
    top: 11px;
    left: 7px;
    transition: 0.3s;
    opacity: 0; }
  a {
    color: #fa7007;
    text-decoration: underline;

    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    line-height: inherit; } }
.modal-content__ui-group-submit {
  margin-top: 10px;
  margin-bottom: 16px;
  @include r($mobile) {
    margin-bottom: 0; }
  .btn {
    max-width: 100%;
    height: 54px;
    background-image: linear-gradient(-5deg, #fa7007 0%, #fa7007 1%, #e9231a 100%);
    color: #fff; } }
.modal-form-tnx__title {
  padding-bottom: 0;
  &::after {
    display: none; } }
