.first-screen {
    margin-top: var(--headerHeight);
    background: url(../img/first-screen-bg.png) no-repeat top center / cover;
    min-height: 910px;
    padding: 84px 0 160px 0;
    @include r($desktop) {
        padding-bottom: 50px; }
    @include r($tablet) {
        margin-top: 0;
        padding-top: 45px; }
    @include r($mobile) {
        padding-top: 84px;
        padding-bottom: 95px;
        background-image: url(../img/first-screen-bg-mob.png); } }
.first-screen__container {
    display: flex;
    position: relative;
    @include r($wide) {
        padding-bottom: 150px; }
    @include r($desktop) {
        flex-direction: column;
        padding-bottom: 0; } }
.first-screen__txt {
    max-width: 880px;
    width: 100%; }
.first-screen__title {
    color: #ffffff;
    font-family: "RotondaC";
    font-size: 24px;
    font-weight: 700;
    line-height: 36px;
    text-transform: uppercase;
    letter-spacing: 0.6px;
    @include r($mobile) {
        font-size: 18px;
        letter-spacing: 0.45px;
        line-height: 30px; } }
.first-screen__wrap-icons {
    margin-top: 30px;
    @include r($mobile) {
        margin-top: 16px; } }
.first-screen__wrap-subtitle {
    margin-top: 20px;
    width: 100%;
    max-width: 780px;
    border-radius: 95px;
    background-color: rgba(#fff,0.36);
    padding: 20px;
    @include r($wide) {
        max-width: 660px; }
    @include r($mobile) {
        border-radius: 73px;
        background-color: #fff;
        padding: 0;
        margin-top: 12px; } }
.first-screen__subtitle {
    background-color: #fff;
    padding: 17px 10px 11px 60px;
    border-radius: 75px;

    color: #464646;
    font-family: "RotondaC";
    font-size: 44px;
    font-weight: 700;
    line-height: 61px;
    letter-spacing: 0.44px;
    @include r($tablet) {
        padding: 15px 5px 15px 40px; }
    @include r($mobile) {
        font-size: 24px;
        letter-spacing: 0.24px;
        line-height: 34px;
        padding: 19px 25px 16px 25px; } }
.first-screen__subtitle_color_orange {
    color: #fe5c14; }
.first-screen__icon {
    max-width: 60px;
    max-height: 48px; }
.first-screen__img {
    top: 170px;
    right: -315px;
    width: 689px;
    height: auto;
    display: block;
    position: absolute;
    @include r(1500) {
        right: -200px; }
    @include r($wide) {
        top: auto;
        bottom: 0; }
    @include r($desktop) {
        position: static; }
    @include r($tablet) {
        max-width: 100%;
        width: 100%; }
    @include r($mobile) {
        margin-top: 30px; } }
.first-screen__item-icon {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    @include r($mobile) {
        margin-bottom: 5px; }
    &:last-child {
        margin-bottom: 0; } }
.first-screen__wrap-icon {
    width: 128px;
    height: 110px;
    background: url(../img/spot.svg) no-repeat center / 100% 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include r($mobile) {
        max-width: 121px;
        min-width: 121px;
        height: 105px;
        margin-left: -10px; } }
.first-screen-icon-txt {
    color: #ffffff;
    font-size: 20px;
    font-weight: 300;
    line-height: 35px;
    margin-left: 11px;
    @include r($mobile) {
        font-size: 18px;
        line-height: 28px; }
    span {
        font-weight: 400;
        display: block; } }
