.footer {
    padding: 30px 0;
    @include r($tablet) {
        padding: 27px 0; } }
.footer__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r($tablet) {
        flex-direction: column; } }
.footer__item {
    width: calc( 100% / 3 );
    display: flex;
    @include r($tablet) {
        width: 100%; } }
.footer__l {
    justify-content: flex-start;
    flex-direction: column;
    @include r($tablet) {
        order: 3;
        justify-content: center; } }
.footer__c {
    justify-content: center;
    @include r($tablet) {
        order: 1;
        margin-bottom: 20px; } }
.footer__r {
    justify-content: flex-end;
    @include r($tablet) {
        order: 2;
        margin-bottom: 18px;
        justify-content: center; } }
.footer__txt {
    color: #343434;
    font-family: "OpenSans";
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.4px;
    margin-bottom: 14px;
    @include r($tablet) {
        text-align: center; }
    &:last-child {
        margin-bottom: 0; }
    a {
        text-decoration: underline;
        color: inherit;
        font-family: inherit;
        font-size: inherit;
        font-weight: inherit;
        letter-spacing: inherit; } }

