.accordion {
    box-shadow: 0 4px 9px 1px rgba(204, 197, 197, 0.48);
    border-radius: 15px;
    background-color: #ffffff;
    padding: 20px;
    @include r($mobile) {
        padding: 20px 9px; } }
.accordion__visible-area {
    position: relative;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    &::after {
        content: '';
        display: block;
        min-width: 35px;
        max-width: 35px;
        height: 35px;
        margin-left: 5px;
        background-image: linear-gradient(-5deg, #fa7007 0%, #fa7007 1%, #e9231a 100%);
        border-radius: 50%;
        @include r($mobile) {
            margin-right: 10px;
            margin-top: 8px;
            margin-left: 25px; } }
    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 19px;
        height: 19px;
        background: url(../img/plus.svg) no-repeat center / 100% 100%;
        top: 8px;
        right: 8px;
        @include r($mobile) {
            right: 18px;
            top: 16px; } } }
.accordion__title {
    color: #3e3e3e;
    font-family: "OpenSans";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px; }
.accordion__slide-area {
    margin-top: 5px;
    display: none;
    @include r($mobile) {
        margin-top: 10px; }
    p {
        margin-bottom: 2px;
        &:last-child {
            margin-bottom: 0; } }
    p, li {
        color: #3e3e3e;
        font-family: "OpenSans";
        font-size: 16px;
        font-weight: 400;
        line-height: 30px; }
    ol {
        list-style-position: inside; }
    ul {
        li {
            &::before {
                content: '- '; } } } }
.accordion_active {
    .accordion__visible-area {
        &::before {
            background: url(../img/minus.svg) no-repeat center / 100% 100%; } } }
