.form-order-call {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; }


.form-order-call__ui-group {
    width: calc( 25% - 15px );
    @include r($desktop) {
        width: 100%;
        margin-bottom: 20px; }
    input:not(.btn) {
        border: 2px solid #ffffff; } }
.form-order-call__ui-group-submit {
    @include r($desktop) {
        margin-bottom: 0; }
    .btn {
        background-color: #fff;
        color: #fc0b1f;
        max-width: 100%;
        font-weight: 400;
        transition: 0.3s;
        &:hover {
            background-color: #581cb4;
            color: #fff; } } }
