.up_btn {
  position: fixed;
  border-radius: 50%;
  background-image: linear-gradient(-5deg, #fa7007 0%, #fa7007 1%, #e9231a 100%);
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  z-index: 100;
  transition: 0.3s;
  display: none;
  &::after {
    position: absolute;
    content: '';
    display: block;
    top: 16px;
    left: 13px;
    width: 12px;
    height: 12px;
    border-left: 2px solid #fff;
    border-bottom: 2px solid #fff;
    transform: rotate(135deg); } }
button {
  padding: 0;
  border: 0;
  box-shadow: none;
  background: none;
  cursor: pointer;
  &:focus {
    outline: none; } }
.btn {
  cursor: pointer;

  display: block;
  padding: 13px 3px;
  width: 100%;
  max-width: 280px;
  border-radius: 24px;
  border: 1px solid transparent;

  font-family: "OpenSans";
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.18px; }

.btn_theme_violet {
  color: #581cb4;
  border: 1px solid #581cb4;
  background-color: #ffffff;
  &:hover {
    border-color: transparent;
    color: #fff;
    background-image: linear-gradient(-5deg, #fa7007 0%, #fa7007 1%, #e9231a 100%); } }

