.steps {
    background: url(../img/advantages-bg.png) no-repeat top 142px center;
    padding-top: 85px;
    @include r($mobile) {
        background-image: url(../img/advantages-bg-mob.png);
        background-position: top 13px center;
        padding-top: 45px; } }
.steps__container {
    position: relative;
    &::before {
        top: 34px;
        left: calc( 100% + 48px );
        content: url(../img/steps-decor-circle-1.png);
        display: block;
        position: absolute;
        @include r($mobile) {
            transform: scale(0.6);
            top: 194px;
            left: -14px; } } }
.steps__section-title {
    margin-bottom: 25px;
    @include r($mobile) {
        margin-bottom: 11px; } }
.steps__subtitle {
    color: #464646;
    font-family: "RotondaC";
    font-size: 24px;
    font-weight: 400;
    letter-spacing: 1.2px;
    text-align: center;
    margin-bottom: 35px;
    @include r($mobile) {
        font-size: 18px;
        line-height: 28px; } }
.steps__subtitle_color_red {
    color: #fc0b1f; }
.steps__navigation {
    height: 270px;
    position: relative;
    @include r($desktop) {
        height: 200px; }
    @include r($tablet) {
        display: none; } }
.steps__line-dots {
    position: absolute;
    left: 0;
    width: 100%;
    top: calc( 50% + 10px );
    transform: translateY(-50%);
    border-top: 1px dashed #bebebe;
    display: flex;
    justify-content: space-between;
    @include r($desktop) {
        display: none; } }
.steps__dot {
    transform: translateY(-50%);
    width: 26px;
    height: 26px;
    box-shadow: 0 0 3px 1px rgba(156, 156, 156, 0.33);
    background-color: #ffffff;
    border-radius: 50%;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #c4c4c4;
        transition: 0.3s; }
    &:first-child {
        margin-left: 55px;
        margin-right: 32px; }
    &:last-child {
        margin-left: 32px;
        margin-right: 55px; } }
.steps__dot_active {
    &::before {
        background-color: #fc0b1f; } }
.steps__pagination {
    display: flex;
    height: 100%;
    align-items: flex-start;
    justify-content: space-between; }
.steps__bullet-wrap {
    position: relative;
    cursor: pointer;
    @include r($desktop) {
        margin-left: -30px;
        z-index: 2; }
    &:nth-child(even) {
        align-self: flex-end; }
    &:first-child {
        margin-right: 33px;
        @include r($desktop) {
            margin-right: 0;

            margin-left: 0; } }
    &:last-child {
        margin-left: 33px;
        @include r($desktop) {
            margin-left: 0; } }
    &:hover {
        .steps__bullet-counter {
            color: #fc0b1f; }
        .steps__bullet-icon {
            svg {
                filter: drop-shadow(0 3px 9px rgba(253, 55, 18, 0.23)); } }
        .steps__bullet-arrow {
            svg {
                fill: #fc0b1f; } } }
    &:nth-child(odd) {
        .steps__bullet-arrow {
            left: calc( 100% + 9px );
            top: 35px;
            transform: rotateX(180deg) rotateZ(-15deg); } }
    &:nth-child(even) {
        .steps__bullet-arrow {
            left: calc( 100% + 5px);
            top: 20px;
            transform: rotateZ(-25deg); } }
    &:last-child {
        .steps__bullet-arrow {
            display: none; } } }
.steps__bullet-wrap_active {
    .steps__bullet-counter {
        color: #fc0b1f; }
    .steps__bullet-icon {
        svg {
            filter: drop-shadow(0 3px 9px rgba(253, 55, 18, 0.23)); } } }
.steps__bullet-icon, .steps__bullet-wrap {
    display: block; }
.steps__bullet-arrow {
    position: absolute;
    top: 0;
    width: 118px;
    height: 57px;
    @include r($wide) {
        width: 88px; }
    svg {
        width: 100%;
        height: 100%;
        fill: #c4c4c4;
        transition: 0.3s; } }
.steps__bullet-icon {
    width: 135px;
    height: 117px;
    svg {
        filter: drop-shadow(0 3px 11px rgba(135, 135, 135, 0.23)); } }
.steps__bullet-counter {
    position: absolute;
    top: calc( 50% - 2px );
    left: 50%;
    transform: translate(-50%,-50%);
    transition: 0.3s;

    color: #c4c4c4;
    font-family: "RotondaC";
    font-size: 55px;
    font-weight: 700; }

.steps__slide {
    text-align: center;
    padding: 42px 100px;
    @include r($desktop) {
        padding: 30px 50px; }
    @include r($tablet) {
        padding-top: 62px;
        padding-left: 14px;
        padding-right: 14px;
        padding-bottom: 23px; } }
.steps__slide-title {
    color: #323232;
    font-family: "OpenSans";
    font-size: 24px;
    font-weight: 600;
    line-height: 34px;
    padding-bottom: 13px;
    margin-bottom: 13px;
    position: relative;
    @include r($mobile) {
        font-size: 20px;
        line-height: 30px; }
    &::after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 80px;
        height: 1px;
        background-color: #fc0b1f; } }
.steps__slide-desc {
    color: #7e7e7e;
    font-family: "OpenSans";
    font-size: 16px;
    font-weight: 400;
    line-height: 26px; }
.steps__wrap-inner-slider {
    max-width: 980px;
    width: 100%;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    @include r($tablet) {
        margin-top: 85px; }
    &::before {
        content: url(../img/steps-decor-spot.png);
        position: absolute;
        top: -56px;
        right: -10px;
        @include r($desktop) {
            transform: scale(0.69);
            top: -63px;
            right: -39px; } }
    &::after {
        content: url(../img/steps-decor-circle-2.png);
        position: absolute;
        top: 48px;
        left: -38px;
        @include r($desktop) {
            content: '';
            display: none; } } }
.steps__slider {
    min-height: 196px;

    box-shadow: 0 3px 6px 1px rgba(252, 11, 31, 0.18);
    border-radius: 98px;
    background-color: #ffffff;
    @include r($mobile) {
        border-radius: 40px; } }
.steps__slider-btn-next, .steps__slider-btn-prev {
    display: none;
    top: calc( 100% + 45px );
    width: 38px;
    height: 38px;
    @include r($tablet) {
        display: flex; }
    svg {
        width: 16px;
        height: 22px; } }
.steps__slider-btn-next {
    right: 0; }
.steps__slider-btn-prev {
    left: 0; }
.steps__bullet-mob {
    display: none; }
.steps__pagination-mob {
    position: absolute;
    top: calc( 100% + 20px );
    left: 50%;
    transform: translateX(-50%);
    display: flex; }
.steps__bullet-mob {
    width: 105px;
    height: 89px;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 3px;
    &::before {
        content: url(../img/steps-spot-static_shadow_black.svg);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 127px;
        display: block; } }
.steps__bullet-mob-counter {
    color: #c4c4c4;
    font-family: "RotondaC";
    font-size: 45px;
    font-weight: 700;
    position: relative;
    z-index: 2; }
.steps__slider__wrap-counter {
    display: none;
    position: absolute;
    z-index: 2;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 119px;
    height: 101px;
    justify-content: center;
    align-items: center;
    @include r($tablet) {
        display: flex; }
    &::before {
        content: url(../img/steps-spot-static_shadow_red.svg);
        width: 135px;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        display: block;
        position: absolute; } }

.steps__slider__counter {
    position: relative;
    z-index: 2;

    color: #fc0b1f;
    font-family: "RotondaC";
    font-size: 55px;
    font-weight: 700;
    margin-bottom: 11px; }
