.package {
    margin-top: 95px;
    @include r($tablet) {
        margin-top: 150px; } }
.package__section-title {
    padding-bottom: 0;
    &::after {
        display: none; } }
.package__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 33px;
    @include r($tablet) {
        flex-direction: column;
        align-items: center;
        margin-top: 0; } }
.package__item {
    width: calc( 50% - 10px );
    box-shadow: 0 14px 20px 1px rgba(117, 102, 128, 0.23);
    border-radius: 30px;
    overflow: hidden;
    background-color: #ffffff;
    position: relative;
    @include r($tablet) {
        width: 100%;
        margin-top: 24px; } }
.package__item-header {
    text-align: center;
    padding: 13px 10px 43px 10px;
    background: url(../img/package-item-bg.png) no-repeat top center / cover;

    color: #464646;
    font-size: 30px;
    font-weight: 600;
    line-height: 44px;
    margin-bottom: 24px;
    @include r($desktop) {
        font-size: 22px; }
    @include r($mobile) {
        letter-spacing: 0.22px;
        line-height: 32px;
        margin-bottom: 14px; }
    span {
        font-weight: 700; } }

.package__item-list {
    margin-bottom: 12px; }
.package__item-li {
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
    letter-spacing: 0.16px;
    position: relative;
    padding-left: 32px;
    margin-bottom: 5px;
    @include r($mobile) {
        margin-bottom: 3px; }
    &:last-child {
        margin-bottom: 0; }
    &::before {
        width: 17px;
        content: url(../img/checkbox.svg);
        display: block;
        position: absolute;
        top: 3px;
        left: 0; } }
.package__item-wrap {
    padding: 0 20px 110px 20px;
    @include r($mobile) {
        padding: 0 10px 89px 10px; } }
.package__item-txt {
    color: #343434;
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.16px;
    margin-bottom: 3px;
    &:last-of-type {
        margin-bottom: 0; } }
.package__item-btn {
    max-width: 380px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 40px;
    @include r($desktop) {
        max-width: calc( 100% - 50px ); }
    @include r($mobile) {
        bottom: 30px; } }
