.sub-header {
    display: none;
    @include r($tablet) {
        padding-top: calc( var(--headerHeight) + 25px );
        padding-bottom: 15px;
        display: block;
        background-color: #fff; }
    @include r($mobile) {
        padding-top: calc( var(--headerHeight) + 5px ); } }
.sub-header__title {
    text-align: center;
    color: #fc0b1f;
    font-family: "RotondaC";
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 1.5px; }
.sub-header__logo {
    display: none;
    @include r($mobile) {
        display: block;
        max-width: 117px;
        margin-top: 7px;
        margin-left: auto;
        margin-right: auto; } }
