.social-network {
    display: flex; }
.social-network__item {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #fc0b1f;
    border: 2px solid #fc0b1f;
    width: 46px;
    height: 46px;
    margin-left: 14px;
    transition: 0.3s;
    &:first-child {
        margin-left: 0; }
    svg {
        width: 25px;
        height: 21px;
        fill: #ffffff;
        transition: 0.3s; }
    &:hover {
        background-color: #fff;
        svg {
            fill: #fc0b1f; } } }
