.advantages {
    margin-top: -25px;
    padding-top: 110px;
    background: url(../img/advantages-bg.png) no-repeat top left / cover;
    @include r($desktop) {
        padding-top: 80px; }
    @include r($mobile) {
        margin-top: -14px;
        background-image: url(../img/advantages-bg-mob.png);
        background-size: 100%; } }
.advantages__section-title {
    max-width: 780px;
    width: 100%;
    margin-left: auto;
    margin-right: auto; }
.advantages__wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 30px;
    @include r($mobile) {
        margin-top: 10px; } }
.advantages__item {
    cursor: pointer;
    box-shadow: 0 7px 9px rgba(123, 99, 142, 0.16);
    border-radius: 20px;
    background-color: #ffffff;
    width: calc( 100% / 3 - 13.3px );
    overflow: hidden;
    margin-top: 20px;
    padding: 36px 23px 32px 29px;
    position: relative;
    @include r($tablet) {
        width: calc( 50% - 10px ); }
    @include r($mobile) {
        width: 100%;
        padding: 25px 19px 23px 19px; }
    &:hover {
        box-shadow: 0 7px 9px rgba(252, 11, 31, 0.23);
        transition: 0.3s;
        .advantages__item-decor_spot {
            svg {
                fill: url(#grad-icon-hover);
                opacity: 0.20; } }
        .advantages__item-decor_circle {
            svg {
                stroke: url(#grad-icon-hover);
                opacity: 0.20; } }
        .advantages__item-counter {
            color: #fc0b1f; } }
    &:nth-child(1) {
        .advantages__item-decor-2 {
            width: 150px;
            height: 127px;
            bottom: calc( 100% - 67px );
            left: calc( 100% - 106px );
            @include r($mobile) {
                bottom: calc( 100% - 87px );
                left: calc( 100% - 65px ); } }
        .advantages__item-decor-1 {
            width: 93px;
            height: 93px;
            top: calc( 100% - 78px );
            left: calc( 100% - 78px );
            @include r($mobile) {
                top: calc( 100% - 66px );
                left: calc( 100% - 52px ); } }
        .advantages__item-decor-3 {
            width: 150px;
            height: 127px;
            top: calc( 100% - 78px );
            right: calc( 100% - 127px );
            @include r($mobile) {
                top: calc( 100% - 68px );
                right: calc( 100% - 127px ); } } }
    &:nth-child(2) {
        .advantages__item-decor-2 {
            width: 150px;
            height: 127px;
            bottom: calc( 100% - 67px );
            left: calc( 100% - 106px );
            transform: rotate(45deg);
            @include r($mobile) {
                bottom: calc( 100% - 87px );
                left: calc( 100% - 65px );
                transform: none; } }
        .advantages__item-decor-1 {
            width: 109px;
            height: 109px;
            bottom: calc( 100% - 36px );
            right: calc( 100% - 105px );
            @include r($mobile) {
                bottom: calc( 100% - 24px );
                right: calc( 100% - 76px ); } }
        .advantages__item-decor-3 {
            width: 150px;
            height: 127px;
            transform: rotate(45deg);
            top: calc( 100% - 55px);
            right: calc( 100% - 144px);
            @include r($mobile) {
                transform: none;
                top: calc( 100% - 75px);
                right: calc( 100% - 113px); } } }
    &:nth-child(3) {
        .advantages__item-decor-2 {
            width: 150px;
            height: 127px;
            bottom: calc( 100% - 45px );
            right: calc( 100% - 131px );
            @include r($mobile) {
                bottom: calc( 100% - 36px );
                right: calc( 100% - 105px ); } }
        .advantages__item-decor-1 {
            width: 93px;
            height: 93px;
            bottom: calc( 100% - 67px );
            left: calc( 100% - 90px );
            @include r($mobile) {
                bottom: calc( 100% - 51px );
                left: calc( 100% - 56px ); } }
        .advantages__item-decor-3 {
            width: 150px;
            height: 127px;
            top: calc( 100% - 83px);
            left: calc( 100% - 72px);
            @include r($mobile) {
                top: calc( 100% - 63px);
                left: calc( 100% - 112px); } } }
    &:nth-child(4) {
        .advantages__item-decor-2 {
            width: 150px;
            height: 127px;
            bottom: calc( 100% - 92px );
            left: calc( 100% - 56px );
            @include r($mobile) {
                bottom: calc( 100% - 83px );
                left: calc( 100% - 53px ); } }
        .advantages__item-decor-1 {
            width: 110px;
            height: 110px;
            right: calc( 100% - 82px );
            bottom: calc( 100% - 39px );
            @include r($mobile) {
                right: calc( 100% - 62px );
                bottom: calc( 100% - 26px ); } }
        .advantages__item-decor-3 {
            width: 150px;
            height: 127px;
            top: calc( 100% - 82px);
            right: calc( 100% - 130px);
            @include r($mobile) {
                top: calc( 100% - 58px);
                right: calc( 100% - 123px); } } }
    &:nth-child(5) {
        .advantages__item-decor-1 {
            width: 110px;
            height: 110px;
            right: calc( 100% - 79px );
            top: calc( 100% - 68px );
            @include r($mobile) {
                right: calc( 100% - 67px );
                top: calc( 100% - 40px ); } }
        .advantages__item-decor-3 {
            width: 150px;
            height: 127px;
            bottom: calc( 100% - 67px);
            left: calc( 100% - 40px);
            @include r($mobile) {
                bottom: calc( 100% - 100px);
                left: calc( 100% - 67px);
                transform: rotate(55deg); } } }
    &:nth-child(6) {
        .advantages__item-decor-1 {
            width: 110px;
            height: 110px;
            right: calc( 100% - 63px );
            top: calc( 100% - 48px );
            @include r($mobile) {
                right: calc( 100% - 67px );
                top: calc( 100% - 40px ); } }
        .advantages__item-decor-2 {
            width: 110px;
            height: 110px;
            left: calc( 100% - 63px );
            bottom: calc( 100% - 73px );
            @include r($mobile) {
                left: calc( 100% - 59px );
                bottom: calc( 100% - 63px ); } }
        .advantages__item-decor-3 {
            width: 150px;
            height: 127px;
            top: calc( 100% - 63px);
            left: calc( 100% - 68px);
            @include r($mobile) {
                top: calc( 100% - 36px);
                left: calc( 100% - 96px);
                transform: rotate(73deg); } } } }
.advantages__item-wrap {
    position: relative;
    z-index: 2; }
.advantages__item-counter {
    color: rgba(#7f00bc,0.35);
    font-family: "RotondaC";
    font-size: 72px;
    font-weight: 700;
    line-height: 61px;
    letter-spacing: 0.72px;
    margin-bottom: 26px;
    transition: 0.3s;
    @include r($mobile) {
        font-size: 60px;
        margin-bottom: 2px; } }
.advantages__item-txt {
    line-height: 28px;
    letter-spacing: 0.05px;
    font-weight: 300; }
.advantages__item-decor-1, .advantages__item-decor-2, .advantages__item-decor-3 {
    position: absolute;
    svg {
        width: 100%;
        height: 100%;
        opacity: 0.63; } }
.advantages__item-decor_circle {
    svg {
        stroke: url(#grad-icon); } }
.advantages__item-decor_spot {
    svg {
        fill: url(#grad-icon); } }
