.header {
    box-shadow: 0 6px 18px rgba(253, 48, 21, 0.24);
    background-color: #ffffff;
    // position: fixed
    position: absolute;
    max-width: inherit;
    width: 100%;
    z-index: 100;
    @include r($mobile) {
        background-color: #fb0a1f;
        box-shadow: none;
        position: fixed; } }
.header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding-top: 19px;
    padding-bottom: 17px;
    @include r($wide) {
        padding-top: 15px;
        padding-bottom: 13px; }
    @include r($tablet) {
        padding-top: 10px;
        padding-bottom: 8px; }
    @include r($mobile) {
        padding-top: 14px;
        padding-bottom: 14px; } }
.header__logo {
    @include r($wide) {
        max-width: 100px; }
    @include r($mobile) {
        display: none; } }
.header__action {
    max-width: 1000px;
    width: 100%;
    @include r($wide) {
        max-width: 800px; }
    @include r($desktop) {
        max-width: 550px;
        padding-right: 60px; }
    @include r($tablet) {
        max-width: 285px; }
    @include r($mobile) {
        max-width: 195px;
        padding-right: 0; } }
.header__top-line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 13px;
    margin-bottom: 13px;
    border-bottom: 1px solid rgba(#b6b6b6,0.29);
    @include r($desktop) {
        padding-bottom: 0;
        margin-bottom: 0;
        border-bottom: none; } }
.header__title {
    color: #fc0b1f;
    font-family: "RotondaC";
    font-weight: 400;
    font-size: 24px;
    letter-spacing: 1.8px;
    @include r($wide) {
        font-size: 21px;
        letter-spacing: 0.5px; }
    @include r($desktop) {
        max-width: 300px; }
    @include r($tablet) {
        display: none; } }

.header__bottom-line.active {
    @include r($desktop) {
        left: 50%;
        transform: translateX(-50%); } }
.header__bottom-line {
    display: flex;
    justify-content: space-between;
    @include r($desktop) {
        transition: 0.3s;
        position: absolute;
        top: 0;
        width: 100vw;
        left: 0;
        transform: translateX(calc( -100% - 300px ));
        padding: var(--headerHeight) 15px 40px 15px;
        right: 0;
        flex-direction: column;
        align-items: center;
        background-color: #fff;
        @include r($mobile) {
            align-items: flex-start;
            padding: var(--headerHeight) 10px 40px 10px; } } }
.header__link {
    color: #333333;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.18px;
    @include r($wide) {
        font-size: 16px;
        letter-spacing: 0; }
    @include r($desktop) {
        font-size: 18px;
        letter-spacing: 0.18px;
        font-weight: 600;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0; } }
    &:hover {
        color: #fc0b1f;
        text-decoration: underline;
        @include r($desktop) {
            text-decoration: none; } } }
.header__btn {
    @include r($desktop) {
        max-width: 200px; }
    @include r($mobile) {
        background-color: transparent;
        border-color: #fff;
        color: #fff; } }
